export default {
  name: "officialCourses",
  data() {
    return {
      option: {
        lastList: [
          { name: "课程章节", class: "", type: true },
          { name: "课程详情", class: "", type: false }
        ]
      }
    };
  },
  //   直接执行
  mounted() {},
  //   方法
  methods: {
    // 公用的选择方法
    selectRankingAll: function(item, index, ListName) {
      const _this = this;
      _this.option[ListName].map(function(event, idx) {
        if (event.name == item.name) {
          event.type = true;
          _this.$set(_this.option[ListName], idx, event);
        } else {
          event.type = false;
          _this.$set(_this.option[ListName], idx, event);
        }
      });
    },
    openCustomHtml: function(name) {
      if (name) {
        if (name == "立即试听") {
          this.$router.push({ path: "/curriculumVideo" });
        } else if (name == "课堂检测") {
          this.$router.push({ path: "/classroomTesting" });
        }
      }
    }
  }
};



